body {
  margin: 0;
  font-family: 'Inter', system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #e7f6ff;
  height: 100%;
}

* {
  font-family: 'Inter', system-ui;
}

.wrapper {
  background: #ffffff;
  box-shadow: 0px 30px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 0 auto;
  margin-top: 20px;
}

//Header
header {
  border-bottom: 1px solid #eaeaea;
  h3,
  p {
    margin: 0;
  }

  .openCart {
    cursor: pointer;
    margin-right: 30px;
    img {
      margin-right: 10px;
    }
  }

  .favorite {
    margin-right: 30px;
    color: #5c5c5c;
    cursor: pointer;
    font-style: 14px;

    span {
      padding-left: 5px;
    }

    &:hover {
      color: #000;
    }
  }

  .orders {
    cursor: pointer;
    &:hover {
      color: #000;
    }
  }
}
//Header

.content {
  h1 {
    margin: 0;
  }
}

.titleCardBlock {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  justify-content: space-between;
}

.searchBlock {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;
  position: relative;

  .clear {
    position: absolute;
    width: 18px;
    height: 18px;
    top: 13px;
    right: 10px;
    cursor: pointer;
  }

  input {
    border: 0;
    padding: 13px;
    font-size: 16px;
    width: 200px;
  }
}
.cardBlock {
  justify-content: space-evenly;
  display: flex;
  flex-wrap: wrap;
  column-gap: 40px;
  row-gap: 40px;
}

.items {
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: space-between;
  overflow: auto;
  margin-bottom: 40px;
}
h2 {
  margin: 0;
  font-size: 24px;
}

.removeBtn {
  opacity: 0.5;
  cursor: pointer;
  transition: opacity 0.15s ease-in-out;

  &:hover {
    opacity: 1;
  }
}
.cartEmpty {
  text-align: center;

  .greenButton {
    position: relative;

    &:disabled {
      background: #ebebeb;
      cursor: normal;
    }

    &:hover {
      img {
        transform: translateX(-5px);
      }
    }
    img {
      position: absolute;
      left: 30px;
      top: 20px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

.cartItem {
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;
  padding: 10px;

  .cartItemImg {
    height: 70px;
    width: 70px;
    background-size: contain;
    background-position: 0 5px;
    background-repeat: no-repeat;
    margin-right: 20px;
  }

  p {
    font-size: 16px;
    margin: 0;
  }
  b {
    font-size: 14px;
  }
}

.cartTotalBlock {
  display: flex;
  flex-direction: column;

  ul {
    display: block;
    margin-bottom: 40px !important;
    li {
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;
    }
    div {
      flex: 1;
      height: 1px;
      border-bottom: 1px dashed #dfdfdf;
      position: relative;
      top: -4px;
      margin: 0 7px;
    }
  }
  .greenButton {
    position: relative;

    &:disabled {
      animation: button-loading 0.6s ease-in-out infinite;
      background: #bebebe !important;
      cursor: default !important;
    }

    &:hover {
      img {
        transform: translateX(5px);
      }
    }
    img {
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

.greenButton {
  width: 100%;
  height: 55px;
  background: #9dd558;
  border-radius: 18px;
  border: 0;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s ease-in-out;

  &:hover {
    background: darken($color: #9dd558, $amount: 10%);
  }
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

//Media queries

@media (max-width: 1080px) {
  .cardBlock {
    column-gap: 30px;
  }
}

@media (max-width: 768px) {
  .titleCardBlock {
    h1 {
      font-size: 24px;
    }

    input {
      width: 150px;
    }
  }

  .favorite {
    span {
      display: none !important;
    }
  }
}

@media (max-width: 640px) {
  .nameShop {
    h3 {
      font-size: 16px;
    }
    p {
      font-size: 14px;
    }
  }
  .titleCardBlock {
    h1 {
      font-size: 16px;
    }

    input {
      width: 130px;
    }
  }
}

@media (max-width: 640px) {
  .nameShop {
    display: none !important;
  }
}
